type SubscriberType = //   
    (messages: Array<ChatMessageType>) //  ,         WS
        => void //    

export type ChatMessageType = { //   
    message: string,
    photo: string,
    userId: number,
    userName: string
}

let subscribers = [] as Array<SubscriberType> //  
let ws: WebSocket | null = null //    websocket

const closeHandler = () => { //    websocket
    console.log("The network connection has been lost.");
    setTimeout(()=>createChannel(),3000) //      
}

const messageHandler = (e: MessageEvent) => { //   
    const newMessages = JSON.parse( e.data ) //    
    subscribers.forEach((s:SubscriberType)=>{ //    
        s(newMessages) //      
    })
}
const openHandler = () => { //     websocket
    setTimeout(()=>{
        console.log( 'open' ) //       
    },2000)
}

const closeChannelCommon = () => {//  ,  
    ws?.removeEventListener( 'open', openHandler )//      websocket
    ws?.removeEventListener( 'message', messageHandler ) //     
    ws?.removeEventListener( 'close', closeHandler )//      websocket
    ws?.close() //   
    window.removeEventListener("offline", closeHandler);//     
}

const createChannel = () => { //     websocket
    ws && closeChannelCommon() //  ,      

    ws = new WebSocket( 'wss://social-network.samuraijs.com/handlers/ChatHandler.ashx' ) //   
    ws?.addEventListener( 'open', openHandler )//      websocket
    ws?.addEventListener( 'message', messageHandler ) //     
    ws?.addEventListener( 'close', closeHandler )//      websocket
    window.addEventListener("offline", closeHandler);//     
}

export const chatAPI = {
    startChannel: () => {
        createChannel() //  
    },
    closeChannel: () => {
        subscribers = [] //      
        closeChannelCommon()//  ,  
    },
    subscribe: (callback: SubscriberType) => { //     
        subscribers.push( callback )//   
        return () => {
            subscribers.filter((s:SubscriberType)=> s !== callback) //   (   unsubscribe)
        }
    },
    unsubscribe: (callback: SubscriberType) => { //     
        subscribers.filter((s:SubscriberType)=> s !== callback) //   ,    
    },
    sendMessage: (message: string) => {
        ws?.send(message) //  
    }
}
