import {getDispatch, getState} from "../../../redux/store-redux";
import {appActions} from "../../../redux/app-reducer";
import {ErrorType} from "../types/commonTypes";

const getUnixTime = () => {
    return new Date().getTime() //         Unix
}

export const saveDataToNotify = (messageItem:string, style: string = "Warning" ) => {
    setTimeout(()=>{
        getDispatch()( appActions.setNotify( //     
            [
                ...getState().app.notify, //   ,      
                {
                    message: messageItem, //   
                    timeUnix: getUnixTime(), //   (   id)
                    style: style
                }
            ]
        ) )
    },30) //     -  
}

export const saveErrorGlobal = (errorGlobal: ErrorType) => {
    getDispatch()( appActions.setAppErrorAC( errorGlobal ) ) //     
}
